ol {
	list-style: none;
	overflow: auto;
	padding: 0;
	height: calc(100% - 30px);
}

h3 {
	padding-left: 10px;
	padding-bottom: 10px;
	border-bottom: 1px solid #e6e6e6;
}

.cluelist {
	height: 100%;
	width: 48%;	
}

@media only screen and (max-width: 1100px) {
    .cluelist {
        height: 50%;
        width: 100%; 
        padding-bottom: 5%;
    }
}
